.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .container {
    padding-left: 34px;
    padding-right: 34px;
  }
}

@media (min-width: 1280px) {
  .container {
    padding-left: 46px;
    padding-right: 46px;
  }
}

:root {
  --vdarkpink: #d31342;
  --pinkbtnhover: #c7133e;
  --greybtnhover: #ecebeb;
  --buttonhome: #d31342;
  --pinkicon: #fea5bb;
  --darkpink: #d41443;
  --pink: #ffa5ba;
  --lightpink: #ffb8ca;
  --modalpink: #f4e6e9;
  --backgroundpink: #fff5f6;
  --white: #fff;
  --lgrey: #fafafa;
  --grey: #d9d9d9;
  --textgrey: #907e82;
  --black: #000;
  --beige: #f0d1a5;
  --mint: #c2e297;
  --modalmenucolor: #e17992;
  --bordermenue: #e18298;
  --footerborder: #e1e1e1;
  --desktop: 1200px;
  --tablet: 768px;
  --mobile: 320px;
  --cubic-bezier: cubic-bezier(.4, 0, .2, 1);
  --titan: "Titan One";
  --shadow: 0px 8px 30px #d414431a;
  --buttonhover: #d41342;
}

body {
  color: var(--black);
  font-family: DM Sans, sans-serif;
  font-weight: 400;
}

li, ul {
  list-style: none;
}

.footer-box {
  background-color: var(--backgroundpink);
}

.footer-box h3 {
  color: var(--pink);
  text-transform: uppercase;
  margin-bottom: 17px;
  font-family: Titan One;
  font-size: 18px;
  line-height: 1.166;
}

.footer-box h2 {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--darkpink);
  margin-bottom: 49.59px;
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.147;
}

.footer-box__social-list {
  gap: 28.24px;
  padding: 0;
  display: flex;
}

.footer-box__social-list-container {
  justify-content: end;
  display: flex;
}

.footer-box__icon-container {
  width: 32px;
  height: 32px;
  background-color: var(--pink);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-box__contact, .footer-box__contact-link {
  text-align: right;
  margin: 46px 0 43px;
  font-family: DM Sans;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.86;
}

.footer-box__contact-link {
  color: var(--darkpink);
  text-decoration: none;
}

.footer-box hr {
  width: 100vw;
  background-color: var(--footerborder);
  height: 1px;
  border: none;
  position: absolute;
  left: 0;
}

.footer-box__all-rights {
  width: auto;
  padding: 22px 0 25px;
}

.footer-box__all-rights p {
  text-align: center;
  color: var(--textgrey);
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.52;
}

@media screen and (min-width: 768px) {
  .footer-box {
    background-color: var(--backgroundpink);
    padding: 0 36px;
  }

  .footer-box__wrapper {
    width: 696px;
    margin: 0 auto;
  }

  .footer-box__upper-part {
    justify-content: space-between;
    display: flex;
  }

  .footer-box h3 {
    margin-bottom: 16px;
    font-size: 26px;
    line-height: 1.145;
  }

  .footer-box h2 {
    letter-spacing: .06em;
    margin-bottom: 164px;
    font-size: 48px;
    line-height: 1.145;
  }

  .footer-box__social-list {
    gap: 30px;
  }

  .footer-box__icon-container {
    width: 34px;
    height: 34px;
  }

  .footer-box__contact, .footer-box__contact-link {
    font-size: 14px;
  }

  .footer-box__all-rights {
    justify-content: center;
    gap: 4px;
    padding: 29px 0 18px;
    display: flex;
  }

  .footer-box__all-rights p {
    font-size: 14px;
    line-height: 1.86;
  }
}

@media screen and (min-width: 1280px) {
  .footer-box {
    background-color: var(--backgroundpink);
    padding: 0 114px 0 116px;
  }

  .footer-box__wrapper {
    width: 1050px;
  }

  .footer-box h3 {
    margin-bottom: 15px;
    font-size: 30px;
    line-height: 1.145;
  }

  .footer-box h2 {
    margin-bottom: 97px;
    font-size: 58px;
    line-height: 1.145;
  }

  .footer-box__line-break {
    display: none;
  }

  .footer-box__social-list {
    gap: 24px;
  }

  .footer-box__icon-container {
    width: 34px;
    height: 34px;
  }

  .footer-box__social-link:hover .footer-box__icon-container, .footer-box__social-link:focus .footer-box__icon-container {
    background-color: var(--buttonhover);
  }

  .footer-box__social-link:focus {
    outline: none;
  }

  .footer-box__contact, .footer-box__contact-link {
    margin-top: 50px;
    font-size: 16px;
  }

  .footer-box__all-rights {
    gap: 6px;
    padding: 9px 0 10px;
  }
}

